export default {
	data() {
		return {
			agreement: '',
			chnerot: '',
			flag: false,
			fllaag: false
		};
	},
	onShow() {
		var that = this;
		uni.request({
			url: this.url + 'usermachine/agreement/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success(res) {
				console.log(res)
				that.chnerot = res.data.data.user_agreement;
				that.agreement = res.data.data.agreement;
				if (that.chnerot == 0) {
					that.flag = true;
				}
			}
		});
	},
	methods: {
		sss: function() {
			var that = this;
			uni.request({
				url: this.url + 'usermachine/agreement/',
				method: 'POST',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				success(res) {
					if (res.statusCode == 200) {
						that.fllaag = true;
						that.flag = false;
						uni.showToast({
							title: '已阅读并同意协议',
							icon:'none',
							duration:3000
						});
					}
				}
			});
		},
		aaa: function() {
			uni.showLoading({
				title: '资源加载中',
				duration: 2000
			});
			uni.downloadFile({
				url: this.urla + 'media/用户服务协议.pdf',

				filePath: wx.env.USER_DATA_PATH + '/用户服务协议.pdf',

				success: res => {
					console.log(res)
					uni.saveFile({
						success: res => {
							tempFilePath: res.tempFilePath,
							//打开文档
							uni.openDocument({
								success: function(res) {
									filePath: res.filePath
								}
							});
						},
					})
					if (res.statusCode === 200) {
						uni.hideLoading()
						uni.showToast({
							title: '下载成功',
							duration: 3000
						});
					}
				},
				fail: function() {
					uni.showToast({
						title: '下载失败',
						icon:'none'
					});
				},
				complete: function() {

				}
			});
		}
	}
};
